/* Container
------------------------------------------------------- */
.cmap-container {
  margin: 0;
  padding: 0;
  font: 13px 'Open Sans', sans-serif;
  position: relative;
}

/* Map
------------------------------------------------------- */
.cmap-container .cmap-map {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: #999;
}

/* Sidebar
------------------------------------------------------- */
.cmap-container .cmap-sidebar {
  position: absolute;
  top: 0;
  left: 0;
  width: 350px;
  background: #eee;
  color: #555;
  margin: 10px;
  border-radius: 3px;
  overflow: hidden;
}

.cmap-sidebar section {
  padding: 5px 10px;
}

.cmap-sidebar section .cmap-heading {
  text-transform: uppercase;
  font-size: 0.9em;
  font-weight: bold;
  margin: 3px 0;
  color: #666;
}

.cmap-sidebar section .cmap-sub-heading {
  font-size: 0.9em;
  margin: 3px 0;
  color: #666;
}

.cmap-sidebar em {
  font-style: italic;
  text-transform: none;
}

/* Vertical list
------------------------------------------------------- */

.cmap-vlist li {
  display: block;
}
.cmap-vlist li:hover {
  font-weight: bold;
}

.cmap-sub-heading {
  padding-left: 0.8em;
}
.cmap-vlist {
  padding-left: 1.6em !important;
}

/* Horizontal list
------------------------------------------------------- */

.cmap-hlist li {
  display: inline-block;
}

.cmap-hlist .cmap-hlist-item {
  display: inline-block;
  padding: 3px 6px;
  background: #dadada;
  border-radius: 3px;
  margin: 2px;
}

.cmap-hlist a.cmap-hlist-item:link {
  text-decoration: none;
  color: #555;
  background: #dadada;
}

.cmap-hlist a.cmap-hlist-item:visited {
  background: #e8e8e8;
  color: #777;
}

.cmap-hlist a.cmap-hlist-item:hover, .cmap-hlist a.cmap-hlist-item:focus, .cmap-hlist label.cmap-hlist-item:hover, .cmap-hlist label.cmap-hlist-item:focus {
  color: #fcfcfc;
  background: #999;
}

.cmap-hlist a.cmap-hlist-item:active {
  background: #888;
  color: #eee;
}

.cmap-hlist label.cmap-hlist-item {
  padding: 3px 6px 3px 3px;
}

.cmap-hlist-item input {
  margin-right: 6px;
}

.cmap-hlist-item .cmap-label-text {
  display: inline-block;
  margin-right: 3px;
}

/* Properties table
------------------------------------------------------- */
.cmap-container .cmap-diff {
  position: absolute;
  z-index: 1;
  top: 10px;
  right: 10px;
}
.cmap-container .cmap-diff-table {
  table-layout: fixed;
  width: 380px;
  border-collapse: collapse;
}
.cmap-container .cmap-diff-metadata,
.cmap-container .cmap-diff-members,
.cmap-container .cmap-diff-tags {
  padding: 10px;
  margin-bottom: 10px;
